import React from "react";

class App extends React.Component {
  constructor(props) {
    super(props);

    this.name = "hello";
  }

  render() {
    return (
      <div>
        <div className="main">
          <h1>Welcome to Kaze</h1>
          <h2>This is where the card will go.</h2>
          <h3>Kaze is a Japanese Learning app built with react!</h3>
        </div>
      </div>
    );
  }
}

export default App;



